footer {
  background: #fafafa;
  border-top: 5px solid #afa62f;
  padding-top: 1rem;
  padding-bottom: 1rem; }
  footer .container {
    padding-left: 1rem;
    padding-right: 1rem; }
  footer h4 {
    background: #736d1f;
    color: #402f1d;
    padding: .5rem; }
  footer ul {
    list-style-type: none;
    padding: 0; }
  footer li {
    padding: .5rem 0; }
  footer .copy-right {
    margin-bottom: 0;
    padding-left: 1rem; }
  footer .address-block {
    margin-bottom: 1rem; }

@media (min-width: 768px) {
  footer h4 {
    margin-top: 1rem; }
  footer .flex {
    display: flex; }
  footer .item {
    width: 200px; }
    footer .item:nth-child(2) {
      margin-top: 1rem;
      width: 300px; }
    footer .item.map {
      padding: 0 1rem; } }

@media (min-width: 992px) {
  footer {
    border-left: 1px solid #c9c9c9;
    border-right: 1px solid #c9c9c9; }
    footer .item {
      width: 20%; }
      footer .item:nth-child(2) {
        width: 53%; } }

@media (min-width: 1200px) {
  footer .item:nth-child(2) {
    width: 54%; } }
