.header-nav {
  overflow: hidden; }
  .header-nav.menu-open .secondary-nav {
    max-height: 1000px; }
  .header-nav nav {
    background: #736d1f;
    border: 1px solid #595418;
    border-left: none;
    border-right: none; }
  .header-nav ul {
    list-style-type: none;
    padding: 0;
    margin: 0; }
    .header-nav ul li {
      line-height: 1rem;
      padding: 1.3rem 1rem;
      border-bottom: 1px solid rgba(0, 0, 0, 0.3); }
  .header-nav a {
    color: white;
    text-decoration: none; }
  .header-nav .main-nav {
    display: flex; }
    .header-nav .main-nav li {
      flex-grow: 1; }
  .header-nav .secondary-nav {
    transition: all .5s ease-in-out;
    max-height: 0; }
    .header-nav .secondary-nav li {
      border-bottom: none; }
  .header-nav .logo {
    color: #e8e3d8;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
    text-decoration: none;
    font-size: 16px;
    font-weight: 700;
    line-height: 2; }
  .header-nav .dropdown {
    text-align: right;
    padding-top: 1rem;
    padding-bottom: 1rem;
    max-width: 50px; }

@media (min-width: 375px) {
  .header-nav .logo {
    font-size: 20px;
    line-height: 1.4; } }

@media (min-width: 768px) {
  .header-nav .dropdown {
    max-width: 60px; } }

@media (min-width: 992px) {
  .header-nav {
    display: flex;
    justify-content: space-between; }
    .header-nav .dropdown {
      display: none; }
    .header-nav .logo {
      font-size: 18px; }
    .header-nav .secondary-nav {
      display: flex;
      max-height: 100px; }
      .header-nav .secondary-nav li {
        flex-grow: 1; }
    .header-nav a {
      line-height: 1.6; }
    .header-nav .social-link {
      width: 170px; } }
