.menu-open .menu-button a {
  top: .6rem; }

.menu-button {
  border: 1px solid black;
  border-radius: 8px;
  padding: .5rem;
  min-height: 1.5rem; }
  .menu-button a {
    display: block;
    position: relative; }
    .menu-button a span {
      height: 2px;
      margin-bottom: .5rem;
      width: 100%;
      background: black;
      display: block; }
      .menu-button a span.menu-open {
        transform: rotate(45deg);
        position: absolute; }
        .menu-button a span.menu-open:last-child {
          transform: rotate(-45deg); }
      .menu-button a span:last-of-type {
        margin-bottom: 0; }

@media (min-width: 768px) {
  .menu-button {
    padding: .8rem; } }
